.tv-programm__chanels-item {
    width: 1200px;
    margin: 20px auto;
}
.tv-programm__channel ul li {
    display: flex;
    color: #fff;
    margin-bottom: 15px;
    width: 25%;
    display: block;
}
.tv-programm__channel ul li span  {
    margin-right: 20px;
}
.program__list-link {
    display: flex;
}
.program__list-item-progress {
    margin: 10px 0;
    height: 4px;
    background: #2a3646;
    width: 100%;
}
.program__list-item-progress-bar {
    height: 4px;
    background: #fff;
}